.blog {
  margin-bottom: 120px;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 70px;
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 20px;
  }
}

.blog__list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 570px);
  grid-template-rows: repeat(2, 300px);
  column-gap: 30px;
  row-gap: 30px;
  padding: 0;

  @media (max-width: $desktop-maket-width) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: $tablet-width-only) {
    grid-template-columns: 1fr;
    row-gap: 50px;
    grid-template-rows: auto;
  }

  @media (max-width: $mobile-width-only) {
    row-gap: 0;
  }
}

.blog__item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  @media (max-width: $mobile-width-only) {
    display: block;
    margin-bottom: 40px;
  }
}

.blog__item--order {
  @media (max-width: $tablet-width-only) {
    order: 2;
  }
}

.blog__item--main {
  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(2, 285px);
    grid-template-rows: repeat(2, 300px);
    row-gap: 30px;
  }
}

.blog__picture {
  background-color: $light-bg-color;
  width: 100%;
  height: 300px;

  @media (max-width: $desktop-maket-width) {
    &:not(.blog__picture--main) {
      max-width: 339px;
      width: 100%;
      height: 300px;

      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }
  }

  @media (max-width: $tablet-width-only) {
    max-width: 339px;
    width: 100%;
    height: 300px;

    & img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  @media (max-width: $mobile-width-only) {
    height: 256px;

    &:not(.blog__picture--main) {
      height: 256px;
    }
  }
}

.blog__picture--main {
  @media (min-width: $desktop-width) {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
  }
}

.blog__picture--order {
  order: 2;
}

.blog__article-wrapper {
  background-color: $light-bg-color;
  height: 300px;
  display: flex;
  flex-direction: column;
  // justify-content: center;
  align-items: center;

  @media (max-width: $mobile-width-only) {
    height: 256px;
  }
}

.blog__article-wrapper--main {
  @media (min-width: $desktop-width) {
    grid-column: 2;
    grid-row: 2;
  }
}

.blog__date {
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  color: $basic-light;
  margin: 0;
  margin-top: 71px;

  @media (max-width: $mobile-width-only) {
    margin-top: 48px;
  }

  &--main {
    @media (max-width: $tablet-width-only) {
      margin-top: 85px;
    }

    @media (max-width: $mobile-width-only) {
      margin-top: 65px;
    }
  }
}

.blog__title {
  font-family: "Cormorant Garamond", "Georgia", serif;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 130%;
  color: $basic-black;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 35px;

  &--main {
    @media (max-width: $tablet-width-only) {
      margin-bottom: 40px;
    }
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 40px;
  }
}

.blog__link {
  text-decoration: none;
  border-bottom: 1px solid $basic-brown;
  padding-bottom: 7px;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: $basic-dark;

  &:hover {
    opacity: 0.5;
  }
}
